import React from 'react';
import { MDIcon } from 'bhp-ui-react';

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  render() {
    return (
      <div className="container-main" style={{height: '200px'}}>
        <div className={this.state.isOpen===true? 'container-sidebar sidebar-show' : 'container-sidebar'}>
          <div className="sidebar-backdrop" onClick={this.toggle}></div>
          <div className="sidebar">
            <a href="">example 1</a>
            <ul>
              <li><a href="">example 2</a></li>
              <li><a href="">example 3</a></li>
              <li><a href="">example 4</a></li>
              <li><a href="">example 5</a></li>
              <li><a href="">example 6</a></li>
              <li><a href="">example 7</a></li>
              <li><a href="">example 8</a></li>
              <li><a href="">example 9</a></li>
              <li><a href="">example 10</a></li>
              <li><a href="">example 11</a></li>
              <li><a href="">example 12</a></li>
              <li><a href="">example 13</a></li>
              <li><a href="">example 14</a></li>
            </ul>
            <div><a href="">example 15</a></div>
            <div><a href="">example 16</a></div>
            <div><a href="">example 17</a></div>
            <div><a href="">example 18</a></div>
            <div><a href="">example 19</a></div>
            <div><a href="">example 20</a></div>
          </div>
        </div>
        <div className="container-body" >
          <span onClick={this.toggle}>
            <MDIcon name='menu' size={48}  />
          </span>
        </div>
      </div>
    )
  }
};



